___CSS_LOADER_EXPORT___.push([module.id, `.loading {
    display: flex;
    justify-content: center;
}

.hidden {
    display: none;
}

body {
    background-color: lavender;
    font-family: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif,
        "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    display: grid;
    grid-template: auto 1fr / 1fr min(800px, 95vw) 1fr;
    grid-template-areas: "header header header"
        ". body .";
    margin: 0;
    padding: 0;
}

.header {
    grid-area: header;
    text-align: center;
    background-color: white;
    margin: 0;
    padding: 20px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.header-logo {
    font-size: 1.5em;
}

input[type="search"] {
    font-size: 1.5em;
    width: 75%;
    border-radius: 5px;
    margin-left: 15px;
    margin-right: 15px;
}

.header button {
    font-size: 1.5em;
}

.current-units-button {
    background-color: lightgray;
}

.body {
    grid-area: body;
}

.card {
    border-radius: 5px;
    background-color: white;
    margin: 5px;
    box-sizing: border-box;
    padding: 10px;
}

.today-header {
    overflow: hidden;
}

.today-body-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.today-header-temp-and-icon {
    display: flex;
    align-items: center;
}

.address {
    background-color: #0000008c;
    color: white;
    font-size: 1.25em;
    margin: -10px;
    padding: 5px;
    margin-bottom: 5px;
}

.temp {
    font-size: 2.5em;
}

.conditions,
.today-header-high-and-low {
    font-size: 1.5em;
}

.today-grid-item {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid black;
    padding: 10px;
}

.today-grid-item:nth-last-child(2),
.today-grid-item:last-child {
    border-bottom: none;
}

.feelslike-and-sunrise-and-sunset {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 20px;
}

.feelslike>div:last-child {
    font-size: 3.5em;
    font-weight: bold;
}

.hourly-forecasts {
    white-space: nowrap;
    overflow-x: scroll;
    overflow-y: hidden;
}

.hourly-forecast {
    display: inline-block;
    background-color: whitesmoke;
    margin: 5px;
    text-align: center;
    border-radius: 5px;
    padding: 5px;
    padding-left: 25px;
    padding-right: 25px;
}

.hourly-temp {
    font-size: 1.5em;
}

.daily-forecast {
    display: flex;
    justify-content: space-between;
    margin: 10px;
    padding: 5px;
    background-color: whitesmoke;
    border-radius: 1em;
    align-items: center;
    font-size: 1.5em;
}

.daily-forecast>span:first-child {
    padding: 10px;
}

.daily-forecast>span:last-child {
    display: flex;
    justify-content: space-between;
    width: 40%;
    align-items: center;
}

.daily-forecast>span:last-child>span:last-child {
    width: 45%;
    display: flex;
    justify-content: space-between;
}

.daily-forecast>span:last-child>span:nth-child(2) {
    font-size: 0.75em;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,0BAA0B;IAC1B;gEAC4D;IAC5D,aAAa;IACb,kDAAkD;IAClD;kBACc;IACd,SAAS;IACT,UAAU;AACd;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;IAClB,uBAAuB;IACvB,SAAS;IACT,aAAa;IACb,aAAa;IACb,qCAAqC;AACzC;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;IAChB,UAAU;IACV,kBAAkB;IAClB,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,uBAAuB;IACvB,WAAW;IACX,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,2BAA2B;IAC3B,YAAY;IACZ,iBAAiB;IACjB,aAAa;IACb,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;;IAEI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,8BAA8B;IAC9B,aAAa;AACjB;;AAEA;;IAEI,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,qBAAqB;IACrB,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,qBAAqB;IACrB,4BAA4B;IAC5B,WAAW;IACX,kBAAkB;IAClB,kBAAkB;IAClB,YAAY;IACZ,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,YAAY;IACZ,YAAY;IACZ,4BAA4B;IAC5B,kBAAkB;IAClB,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,UAAU;IACV,mBAAmB;AACvB;;AAEA;IACI,UAAU;IACV,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,iBAAiB;AACrB","sourcesContent":[".loading {\n    display: flex;\n    justify-content: center;\n}\n\n.hidden {\n    display: none;\n}\n\nbody {\n    background-color: lavender;\n    font-family: system-ui, \"Segoe UI\", Roboto, Helvetica, Arial, sans-serif,\n        \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\";\n    display: grid;\n    grid-template: auto 1fr / 1fr min(800px, 95vw) 1fr;\n    grid-template-areas: \"header header header\"\n        \". body .\";\n    margin: 0;\n    padding: 0;\n}\n\n.header {\n    grid-area: header;\n    text-align: center;\n    background-color: white;\n    m